<template>
  <div class="box">
    <div class="new-business"
         @click="showModel = true"> + 添加员工 </div>
    <div class="staff-group">
      <van-cell center
                value="内容"
                v-for="item in workerList"
                :key="item.id">
        <template #title>
          <div class="temp">
            <div class="custom-title">
              {{item.sysUser.realName || item.sysUser.nickName}}
              <span class="cus-role">{{item.role | userRole}}</span>
              <div class="cr-phone">{{item.sysUser.phone}}</div>
            </div>
            <van-icon @click="_del(item)"
                      name="delete" />
          </div>
        </template>
      </van-cell>
    </div>
    <van-dialog v-model="showModel"
                closeOnClickOverlay
                @confirm="onSubmit"
                title="添加员工">
      <p class="model-tips">请确保已在哎哟有房小程序完成手机号绑定</p>
      <van-form>
        <van-field readonly
                   clickable
                   name="role"
                   style="text-align: left"
                   input-align="right"
                   right-icon="arrow-down"
                   :value="role"
                   label="角色"
                   @click="showPicker = true" />
        <van-field v-model="query"
                   style="text-align: left"
                   input-align="right"
                   label="电话或姓名"
                   right-icon="search"
                   @click-right-icon="searchFn"
                   placeholder="请输入电话或姓名">

        </van-field>
        <div class="checkbox-group">
          <van-checkbox-group v-model="userList">
            <van-checkbox v-for="item in userAllArray"
                          :key="item.id"
                          :name="item.id"> {{item.userName}} {{item.phone}}</van-checkbox>
          </van-checkbox-group>
        </div>
      </van-form>
    </van-dialog>
    <van-popup v-model="showPicker"
               position="bottom">
      <van-picker show-toolbar
                  :columns="columns"
                  @confirm="onConfirm"
                  @cancel="showPicker = false" />
    </van-popup>
  </div>
</template>
<script>
import Vue from 'vue';
import { PostBusinessCooperationWorkerList, GetBusinessCooperationWorkerSearch, PostBusinessCooperationWorkerAdd, PostBusinessCooperationWorkerRemove } from '@/api/api_business';
import { GetSystemDictDataByType } from '@/api/api_query';
import { Dialog } from 'vant';
import { Toast } from 'vant';
Vue.use(Toast);
let roleArray = [];
export default {
    name: 'staff',
    data() {
        return {
            showModel: false,
            showPicker: false,
            workerList: [],
            query: '', //搜索条件
            columns: [],
            columnsAll: [],
            userList: [],
            userAllArray: [],
            dataForm: {
                cooperationId: '',
                role: '',
                workerIds: ''
            }
        };
    },
    computed: {
        role: {
            get() {
                if (this.columnsAll && this.columnsAll.length) {
                    return this.columnsAll.find((item) => this.dataForm.role == item.dictValue).dictLabel;
                }
            },
            set(val) {}
        }
    },
    created() {
        this.$store.commit('setHeader', '员工管理');
        this.dataForm.cooperationId = this.$route.query.cooperationId;
        this.getWorkerMsg(this.$route.query.cooperationId);
        this.getTypeBusinessBill();
    },
    filters: {
        userRole(val) {
            if (val && roleArray.length) {
                return roleArray.find((item) => item.dictValue == val).dictLabel;
            }
        }
    },
    methods: {
        //查询员工列表
        getWorkerMsg(id) {
            PostBusinessCooperationWorkerList({ cooperationId: id }).done((res) => {
                this.workerList = res.data;
            });
        },

        //删除
        _del(item) {
            Dialog.confirm({
                title: '提示',
                message: `确认删除${item.sysUser.realName || item.sysUser.nickName}`
            })
                .then(() => {
                    PostBusinessCooperationWorkerRemove({ cooperOpRelationId: item.id }).done(() => {
                        Toast.success('操作成功');
                        this.getWorkerMsg(this.$route.query.cooperationId);
                    });
                })
                .catch(() => {
                    Toast('取消操作');
                });
        },
        //查询字典
        getTypeBusinessBill() {
            GetSystemDictDataByType({ dictType: 'business_hotel_role' }).done((res) => {
                this.dataForm.role = res.data[0].dictValue;
                this.columns = res.data.map((item) => item.dictLabel);
                this.columnsAll = roleArray = res.data;
            });
        },
        //搜索
        searchFn() {
            GetBusinessCooperationWorkerSearch({ query: this.query }).done((res) => {
                this.userAllArray = res.data;
            });
        },
        //提交表单
        onSubmit() {
            let obj = { ...this.dataForm, workerIds: this.userList.join(',') };
            PostBusinessCooperationWorkerAdd(obj).done((res) => {
                Toast.success('操作成功');
                this.getWorkerMsg(this.$route.query.cooperationId);
            });
        },
        //确认选择角色
        onConfirm(val) {
            this.dataForm.role = this.columnsAll.find((item) => item.dictLabel == val).dictValue;
            this.showPicker = false;
        }
    }
};
</script>
<style scoped>
.box {
    height: calc(100vh - 8%);
}
.temp {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.new-business {
    height: 2.4rem;
    background: #fff;
    font-size: 0.9rem;
    line-height: 2.4rem;
    color: #000;
    margin: 0.2rem 0;
}
.staff-group {
    height: calc(100vh - 8% - 2.8rem);
    overflow-y: auto;
}
.model-tips {
    font-size: 0.6rem;
    margin-bottom: 1rem;
}
.checkbox-group {
    text-align: right;
    width: 100%;
    display: flex;
    margin-left: 6rem;

    box-sizing: border-box;
}
.van-checkbox-group {
    width: auto;
    text-align: right;
}
.van-checkbox {
    margin: 0.6rem 0;
    background: #f8f8f8;
    width: 10rem;
    padding: 0.3rem;
    border-radius: 0.2rem;
}
.cus-role {
    color: #02bc6c;
    font-size: 0.6rem;
    display: inline-block;
    line-height: 1;
    border: 0.05rem solid #02bc6c;
    border-radius: 0.5rem;
}
.custom-title {
    font-size: 0.8rem;
    font-weight: 500;
}
.cr-phone {
    color: #888888;
    font-size: 0.8rem;
}
</style>



